html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-wrapper {
  height: 40px;
  padding: 10px 0;
}

.nav-li {
  height: 60px;
  margin-right: 20px;
  display: inline-block;
}

.nav-ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav-li:last-child {
  margin-right: 0;
}

.nav-link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
}

.nav-link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

